import { gql } from '@apollo/client';

// ============================================================================
// QUERIES
// ============================================================================

export const GET_BLOGS = gql`
  query GetBlogs(
    $page: Int
    $limit: Int
    $search: String
    $categoryId: ID
    $sort: String
  ) {
    blogs(
      page: $page
      limit: $limit
      search: $search
      categoryId: $categoryId
      sort: $sort
    ) {
      items {
        id
        title
        slug
        shortDescription
        excerpt
        author
        thumbnailUrl
        viewCount
        publishedAt
        category {
          id
          name
          slug
        }
        tags {
          id
          name
          slug
        }
        isFeatured
        isPublished
      }
      total
      page
      pageSize
      totalPages
      hasMore
    }
  }
`;

export const GET_BLOG_BY_SLUG = gql`
  query GetBlogBySlug($slug: String!) {
    blogBySlug(slug: $slug) {
      id
      title
      slug
      content
      shortDescription
      excerpt
      author
      thumbnailUrl
      bannerUrl
      viewCount
      publishedAt
      updatedAt
      category {
        id
        name
        slug
      }
      tags {
        id
        name
        slug
      }
      isFeatured
      isPublished
      metaTitle
      metaDescription
      metaKeywords
      createdAt
    }
  }
`;

export const GET_FEATURED_BLOGS = gql`
  query GetFeaturedBlogs($limit: Int) {
    featuredBlogs(limit: $limit) {
      id
      title
      slug
      shortDescription
      thumbnailUrl
      author
      publishedAt
      category {
        id
        name
        slug
      }
    }
  }
`;

export const GET_BLOGS_BY_CATEGORY = gql`
  query GetBlogsByCategory($categoryId: ID!, $limit: Int, $page: Int) {
    blogsByCategory(categoryId: $categoryId, limit: $limit, page: $page) {
      items {
        id
        title
        slug
        shortDescription
        excerpt
        author
        thumbnailUrl
        viewCount
        publishedAt
        category {
          id
          name
          slug
        }
        isFeatured
      }
      total
      page
      pageSize
      totalPages
      hasMore
    }
  }
`;

export const GET_RELATED_BLOGS = gql`
  query GetRelatedBlogs($categoryId: ID!, $excludeBlogId: ID!, $limit: Int) {
    relatedBlogs(categoryId: $categoryId, excludeBlogId: $excludeBlogId, limit: $limit) {
      id
      title
      slug
      shortDescription
      thumbnailUrl
      author
      publishedAt
      viewCount
      category {
        id
        name
        slug
      }
    }
  }
`;

export const GET_BLOG_CATEGORIES = gql`
  query GetBlogCategories {
    blogCategories {
      id
      name
      slug
      description
      thumbnail
    }
  }
`;

// ============================================================================
// TYPES
// ============================================================================

export interface BlogCategory {
  id: string;
  name: string;
  slug: string;
  description?: string;
  thumbnail?: string;
}

export interface BlogTag {
  id: string;
  name: string;
  slug: string;
}

export interface Blog {
  id: string;
  title: string;
  slug: string;
  content: string;
  shortDescription: string;
  excerpt?: string;
  author: string;
  thumbnailUrl: string;
  bannerUrl?: string;
  viewCount: number;
  publishedAt: string;
  updatedAt: string;
  category: BlogCategory;
  tags: BlogTag[];
  isFeatured: boolean;
  isPublished: boolean;
  metaTitle?: string;
  metaDescription?: string;
  metaKeywords?: string;
  createdAt: string;
}

export interface BlogsResponse {
  items: Blog[];
  total: number;
  page: number;
  pageSize: number;
  totalPages: number;
  hasMore: boolean;
}

export interface BlogBySlugResponse {
  blogBySlug: Blog;
}

export interface FeaturedBlogsResponse {
  featuredBlogs: Blog[];
}

export interface BlogsByCategoryResponse {
  blogsByCategory: BlogsResponse;
}

export interface RelatedBlogsResponse {
  relatedBlogs: Blog[];
}

export interface BlogCategoriesResponse {
  blogCategories: BlogCategory[];
}
